import React from 'react';

function CardProduct({product}){
    
    console.log(product.update)
        return ( 
            <div className="col-md-4 card" style={{margin:5}}>
                <h3>{product.name}</h3>
                <img src={product.image} alt=""/>
                <h3>{product.price}</h3>
                <h3>{product.categori}</h3>
                <h3>{product.stock}</h3>
            </div>
         );
}
 
export default CardProduct;