import React, {Component} from 'react';
import Axios from 'axios'

class AddProduct extends Component {

    state = { 
        'nameProduct' : '',
        'img' : '',
        'price' : '',
        'category' : '',
        'stock': ''
     }


     handlerChange=(e) => {
         this.setState({
             [e.target.name] : e.target.value
         })
     }

     handlerSubmit=()=>{
         // eslint-disable-next-line no-restricted-globals
         event.preventDefault()
         Axios.post("localhost:8080/api/v1/product",this.state)
     }





    render() { 
        return ( 
            <div className="container">
                <h2>Tambah Product</h2>
                <form onSubmit={this.handlerSubmit}>
                    <table>
                        <tbody>
                        <tr>
                            <td>name product</td>
                            <td><input type="text" name="nameProduct" onChange={this.handlerChange}></input></td>
                        </tr>
                        <tr>
                            <td>Image</td>
                            <td><input type="file" name="img" onChange={this.handlerChange}></input></td>
                        </tr>
                        <tr>
                            <td>price</td>
                            <td><input type="text" name="price" onChange={this.handlerChange}></input></td>
                        </tr>
                        <tr>
                            <td>category</td>
                            <td>
                                <select name="category" onChange={this.handlerChange}>
                                    <option>makanan</option>
                                    <option>minuman</option>
                                </select>
                            </td>
                        </tr>
                        <tr>
                            <td>stock</td>
                            <td><input type="text" name="stock" onChange={this.handlerChange}></input></td>
                        </tr>
                        <tr>
                            <td></td>
                            <td><input type="submit" className="btn btn-primary"></input></td>
                        </tr>
                        </tbody>
                    </table>
                </form>
            </div>
         );
    }
}
 
export default AddProduct;